.footer {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__heading {
    margin-bottom: 3rem;
    font-size: 1.8rem;
  }

  &__icon-container {
    width: 18rem;
    display: flex;
    justify-content: space-around;

    .footer__icon {
      height: 5rem;
    }
  }
}
