@use "../../styles/_variables.scss" as *;

.projects {
  margin-top: 5rem;

  &__heading {
    color: $colour-primary;
    margin-bottom: 2rem;
    font-weight: bold;
    font-size: 1.8rem;
  }

  &__tile {
    &--upper {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin-bottom: 0.5rem;
      border-radius: 0.5rem 0.5rem 0rem 0rem;
      background-color: $colour-primary;
      min-height: 78vh;
    }

    &--lower {
      display: flex;
      justify-content: space-between;
      padding: 0.5rem 1rem;
      border-radius: 0rem 0rem 0.5rem 0.5rem;
      background-color: $colour-primary;
      height: 16vh;
      align-items: center;
    }
  }

  &__image-container {
    margin: 0.5rem;
    margin-bottom: 0.25rem;
    background-color: $colour-background;
    min-height: 50%;

    .projects__image {
      object-fit: scale-down;
      height: 100%;
      width: 100%;
    }
  }

  &__info {
    min-height: 50%;
    margin: 0.5rem;
    margin-top: 0.25rem;
    background-color: $colour-background;
    display: flex;
    flex-direction: column;
    padding: 1rem;
    justify-content: space-between;
    align-items: center;
  }

  &__title {
    font-size: 1.2rem;
  }

  &__description {
    margin-top: 1rem;
    margin-bottom: 1rem;
  }

  &__stack-container {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    flex-wrap: wrap;
    gap: 0.5rem;
  }

  &__tool {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0.25rem;
    background-color: $colour-tertiary;
    color: $colour-font-general;
    padding: 0.5rem;
    font-weight: bold;
  }

  &__arrow {
    border: none;
    border-radius: 50%;
    height: 4rem;
    width: 4rem;
    background-color: $colour-background;
    cursor: pointer;

    &--left {
      padding-right: 0.5rem;
    }
    &--right {
      padding-left: 0.5rem;
    }

    .arrow-image {
      height: 1.8rem;
    }
  }

  &__buttons-container {
    width: 50%;
    max-width: 10rem;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }

  &__button {
    height: 2.7rem;
    background-color: $colour-secondary;
    color: white;
    border: none;
    border-radius: 1rem;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;

    .preview-image {
      height: 1.5rem;
    }
  }
}

@media screen and (min-width: $tablet-breakpoint) {
  .projects {
    &__heading {
      font-size: 2rem;
      margin-top: 0rem;
    }
  }
}

@media screen and (min-width: $desktop-breakpoint) {
  .projects {
    &__tile {
      &--upper {
        display: flex;
        flex-direction: row;
        height: 70vh;

        .projects__info {
          padding: 3rem;

          .projects__title {
            font-size: 1.8rem;
          }

          .projects__description {
            font-size: 1.2rem;
            line-height: 1.5rem;
          }
        }
      }

      &--lower {
        height: 12vh;
        display: flex;
      }
    }

    &__image-container {
      height: auto;
      width: 50%;
      margin: 1rem;
      margin-right: 0.5rem;
    }

    &__info {
      height: auto;
      width: 50%;
      margin: 1rem;
      margin-left: 0.5rem;
    }

    &__buttons-container {
      flex-direction: row;
      align-items: center;
      max-width: 17rem;
    }

    &__button {
      width: 7.5rem;
    }
  }
}
